.main-navigation {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-navigation {
    display: grid;
    grid-template-columns: 370px 1fr;
    grid-auto-flow: dense;
  }
}
@media (min-width: 1150px) {
  .main-navigation {
    display: grid;
    grid-template-columns: 1fr 148px 1fr;
